<template>
<div id="game-page-div">
  <div id="titles">
    <h4>DGSL Text Adventure Engine</h4>
  </div>

  <div id="game">
    <div id="game-history" class="game-element">
      <p v-for="item in history" v-bind:key="item" style="padding: 0em 1em;">
        {{ item }}
      </p>
    </div>

    <input id="action-input" class="game-element" type="text" v-model="action"
        placeholder="Do something" maxLength=200>
    <button id="take-action" class="game-element btn btn-sm btn-primary" v-on:click="parseAction">
      Take Action
    </button>
  </div>

</div>
</template>

<script>
export default {
  name: 'game-page',
  data () {
    return {
      history: ['First turn', 'second turn', 'ect', 'ajflsajflfjasfl', 'afjslafjasl', 'afasfdafas', 'afljslfjlsfjlas', 'alfjlsjflsajflajsf', 'this is invisilbe'],
      action: ''
    }
  },
  methods: {
    parseAction () {
      // will need to be replaced with a vuex action
      if (this.action !== '') {
        this.history.push('> ' + this.action)
        this.action = ''
      }
      // Keep the history scrolled all the way
      this.$nextTick(function () {
        const elem = document.getElementById('game-history')
        elem.scrollTop = elem.scrollHeight
      })
      // Focus input again if we pressed the button
      this.focusInput()
    },
    /**
     * Calls parseAction if the ENTER key is pressed.
     */
    enterEvent (event) {
      if (event.keyCode === 13) {
        event.preventDefault()
        this.parseAction()
      }
    },
    /**
     * Gives focus to the input element
     */
    focusInput () {
      const input = document.getElementById('action-input')
      input.focus()
    }
  },
  mounted () {
    // We want ENTER to parse an action
    const page = document.getElementById('game-page-div')
    page.addEventListener('keyup', this.enterEvent)

    // We want the input box focused right away
    this.focusInput()
  }
}
</script>

